@import url(https://fonts.googleapis.com/css?family=Roboto:500);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  background-image: linear-gradient(90deg, #d26742,#3aa0e4 );
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

.App-logo {
  text-align: center;
  margin-bottom: 10px;
}

.App-logo img{
  margin: 0 auto;
  width: 200px;
  height: 200px;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city, .number-of-events {
  width: 250px;
}

.city-input{
  margin-bottom: 0px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  justify-items: center;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 270px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin-top: 10px;
  top: 65px;
  left:0px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
  list-style: none;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #f3a9a9;
}

.event-list-wrapper, .charts-wrapper{
  background-color: rgba(0, 0, 0, 0.198);
  padding: 20px 15px;
  border-radius: 15px;
  text-align: center;
  margin-top: 50px;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 20px;
  text-align: left;
  border-radius: 4px;
  position: relative;
  color: #eee;
  background-image: linear-gradient(45deg,#cf5a33c2,#2368a8af );
  box-shadow: 5px 5px 10px rgba(218, 210, 210, 0.479);
}

.location, .event-description {
  margin-bottom: 20px;
}

.extra-details a{
  color: rgb(134, 192, 240);
}

.show-details, .hide-details {
  position: absolute;
  left: 8px;
  bottom: 10px;
  margin-top: 5px;
  padding: 6px 8px;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
  color: #eee;
  background-color:rgba(4, 121, 255, 0.116);
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.NumberOfEvents {
  width: 250px;
  margin: 60px auto;
  text-align: center;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
@keyframes nprogress-spinner {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.WelcomeScreen-wrapper{
  text-align: center;
  width: 100%;
  height: 100%;
}
.WelcomeScreen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(90deg, #d26742,#3aa0e4 );
  background-size: cover;
  font-family: 'Roboto', sans-serif;
}

.google-btn {
width: 184px;
height: 42px;
margin: 1rem;
background-color: #4285f4;
border-radius: 2px;
box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
  background: #1669f2;
}

.google-icon-wrapper {
position: absolute;
margin-top: 1px;
margin-left: 1px;
width: 40px;
height: 40px;
border-radius: 2px;
background-color: #fff;
}

.google-icon {
position: absolute;
margin-top: 11px;
margin-left: -8px;
width: 18px;
height: 18px;
}

.btn-text {
float: right;
color: #fff;
font-size: 14px;
letter-spacing: 0.2px;
font-family: "Roboto";
background-color: #4285f4;
width: 80%;
height: 100%;
text-align: center;
}

  
